*{
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}
body{
  background-color: #F2F2F2;
  font-family: monospace;
}
.light{
  background-color: transparent;
  transition: all .2s ease-out;
}
.dark{
  background-color: rgba(23, 23, 30, .95);
  transition: all .3s;
}
.dark-text{
  color: #7C7C80;
}
.light-text{
  color: #000;
}
.dark-icon{
  fill: #79797E;
}
.light-icon{
  fill: #000;
}
button:hover{
  cursor: pointer;
}
.activeLink, .activeLink svg path{
  color: #FF0000;
  fill: #FF0000;
  opacity: 1;
}
.dark-header{
  background-color: rgba(23, 23, 30, 1);
}
.light-header{
  background-color: #F2F2F2;
}
