.header{
    padding: 34px 32px;
    display: flex;
    align-items: center;
    justify-content: space-between;
    position: sticky;
    top: 0;
    width: 100%;
    z-index: 200;
    overflow: hidden;
}
.bar-icon-wrapper{
    display: flex;
    align-items: center;
}
.bar-icon{
    border: none;
    background: none;
}
.bar-icon img{
    width: 20px;
    height: 17px;
}
.logo{
    margin-left: 28px;
}
.header__search{
    position: relative;
    display: flex;
    align-items: center;
    margin-left: -100px;
}
.searchInput{
    padding: 12px 24px;
    width: 830px;
    border: none;
    outline: none;
    font-size: 18px;
    color: #fff;
    background-color: rgba(63, 63, 73, 0.2);
    border-radius: 22px;
}
.searchIconBtn{
    background: none;
    border: none;
    outline: none;
    position: absolute;
    right: 20px;
}
.header__adminPanels{
    width: 250px;
    display: flex;
    justify-content: space-between;
    align-items: center;
}
.header__adminPanels button{
    background: none;
    border: none;
    outline: none;
}
.logo{
    display: flex;
    justify-content: space-between;
    align-items: center;
}
.logo h2{
    margin-left: 10px;
    font-size: 25px;
}