.HomeContent{
    margin: 0px auto;
    height: 100vh;
}
.photosWrapper{
    display: flex;
    flex-wrap: wrap;
    justify-content: space-between;
    padding: 30px;
}
.HomePage{
    display: flex;
}
.photosWrapper li{
    margin: 10px;
    max-width: 250px;
    overflow: hidden;
}   
.photo img{
    width: 250px;
    height: 150px;
}
.photo{
    text-decoration: none;
}