::-webkit-scrollbar {
    width: 5px;
  }
  /* Handle */
::-webkit-scrollbar-thumb {
    background: #888; 
    border-radius: 15px;
  }
  ::-webkit-scrollbar-track {
    background: transparent; 
  }
.menu{
    position: -webkit-sticky;
    position: fixed;
    top: 110px;
    left: 0px;
    z-index: 200;    
    width: 270px;
    height: 90vh;
    overflow-y: auto;
    padding: 20px 0;
    display: none;
}
.menu-active{
    transition: 0.2s all;
    display: block;
}
.public-pages ul{
    padding: 8px 22px;
}
.public-pages ul li{
    list-style: none;
    font-size: 16px;
    margin: 10px 0;
}
.public-pages ul li a{
    text-decoration: none;
    font-size: 18px;
}
.public-pages svg{
    margin-right: 10px;
}
.userPhoto{
    width: 50px;
    height: 50px;
    border-radius: 50%;
    flex-direction: column;
    margin-right: 10px;
}
.userAccounts{
    display: flex;
    align-items: center;
    margin: 10px 0;
}